import { Component, Input, OnInit } from '@angular/core';
import { Router, NavigationEnd, Route, ActivatedRoute } from '@angular/router';
import { distinctUntilChanged, filter, tap } from 'rxjs/operators';

@Component({
  selector: 'app-recursive-menu',
  template: `
    <li nz-submenu *ngIf="item.children && item.display" [nzOpen]="isParentOpen(item)" [nzTitle]="item.title">
      <ul>
        <app-recursive-menu *ngFor="let child of item.children" [item]="child"></app-recursive-menu>
      </ul>
    </li>
    <li class="font-semibold" nz-menu-item *ngIf="!item.children" [nzSelected]="isSelected(item)" (click)="navigate(item)">
      {{ item.title }}
    </li>
  `
})
export class RecursiveMenuComponent implements OnInit {
  @Input() item: any;
  selectedPath: string = '';

  constructor(private router: Router, private route: ActivatedRoute) {}

  ngOnInit() {
    this.router.events.pipe(
      distinctUntilChanged(),
      // filter(event => event instanceof NavigationEnd)
    ).subscribe((event: any) => {
      this.selectedPath = event.routerEvent?.url;
    });
  }

  isSelected(item: any): boolean {
    return this.selectedPath === item.path;
  }

  isParentOpen(item: any): boolean {
    // Check if any of the children's path matches the selectedPath
    return item.children?.some((child: any) => this.selectedPath?.includes(child.path));
  }

  navigate(item: any): void {
    this.router.navigate([item.path], { relativeTo: this.route });
  }
}
