import { Router, ActivatedRoute } from '@angular/router';
import {
  ChangeDetectionStrategy,
  Component,
  OnInit,
  inject,
  DestroyRef,
  ChangeDetectorRef,
} from '@angular/core';
import { LocalStorageService } from '@icom/services/authenticate';
import { IcomLibsProjectLoginService } from '@icom/services/projects';
import { Observable, of, take } from 'rxjs';
import { NzIconService } from 'ng-zorro-antd/icon';
import { Breadcrumb, BreadcrumbService } from '@icom/services/common';
import { NzModalService } from 'ng-zorro-antd/modal';
import { environment } from 'libs/core/environments/environment';
@Component({
  selector: 'icom-libs-components-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class IcomLibsComponentsLayoutComponent implements OnInit {
  protected userName!: string;
  protected version!: string;
  protected selectedMenuItem: any;
  protected selectedItem: string | null = null;
  protected menuData: any[] = [];
  protected breadcrumbs$: Observable<Breadcrumb[]>;

  protected destroyRef = inject(DestroyRef);
  protected cacheService = inject(LocalStorageService);
  protected icomLibsProjectLoginService = inject(IcomLibsProjectLoginService);
  protected cdr = inject(ChangeDetectorRef);

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NzModalService,
    private breadcrumbService: BreadcrumbService) {
    this.breadcrumbs$ = this.breadcrumbService.breadcrumbs$;
  }

  ngOnInit(): void {
    this.menuData = this.cacheService.getItem('menu');
    this.menuData.forEach((menu) => {
      menu.display = true;// Gán biến display
    });
    if(environment.module !== 'ep') {
      return;
    }
    this.menuData.forEach((menu) => {
      menu.display = false;// Gán biến display
    });
    const isEp = this.menuData.find(item => item.path === '/section');
    isEp.display = true;
    const token = this.cacheService.getTokenData();
    this.version = 'v1.3.3';
    this.userName = token.username;

  }

  protected changePassword(): void {
    this.router.navigate(['/user/change-password']);
  }

  protected handlerNavigate(child: any, parent: any): void {
    this.openHandler(parent);
    this.selectedMenuItem = child;
    this.router.navigate([child.path], { relativeTo: this.route });
  }

  protected logout() {
    this.icomLibsProjectLoginService.logout();
  }

  protected   showConfirmModal(url: string): void {
    this.modalService.confirm(
      {
        nzTitle: `Bạn có chắc chắn muốn rời trang ? `,
        nzOkText: 'Đồng ý',
        nzOkType: 'primary',
        nzMaskClosable: false,
        nzClosable: false,
        nzOnOk: () => {
          this.router.navigateByUrl(url);
        },
        nzCancelText: 'Hủy bỏ',
      },
      'info'
    );
  }


  protected openHandler(item: any): void {
    this.menuData.forEach((menu) => {
      menu.open = false;
    });
    item.open = true;
  }
}
